[type="checkbox"] {
    display: none;
}

.icon__close {
    position: absolute;
    cursor: pointer;
    right: 21px;
    top: 21px;
    border-radius: 50%;
    line-height: 20px;
    text-align: center;
    font-size: 20px;
}

.navigation {
    position: absolute;
    top: 0;
    left: 0;
    width: 30%;
    height: 100vh;
    background-color: #181717;
    z-index: 3;
    transform: translateX(-110%);
    transition: transform 1s;
}

.icon__menu {
    cursor: pointer;
}

input[type="checkbox"]:checked~nav {
    transform: translateX(0);
    z-index: 2;
    top: 0;
    left: 0;
}